import Menu from '@/components/Menu'

export default {
  components: {
    Menu
  },
  methods:{
    toPersonalData() {
      //跳转到个人资料
      this.$router.push({name:'personalData'})
    },
    toOrder(index){
      //跳转到订单管理
      this.$router.push({name:'order',query:{index:index}})
    },
    toAddressManage(){
      //跳转到地址管理
      this.$router.push({name:'addressManage'})
    }
  }

}

